import { useQuery } from 'react-query';
import { createSelector } from 'reselect';
import keys from './query-key-factory.js';

const selectDataSets = data => data.dataSets;

const selectCustomForms = createSelector(selectDataSets, dataSets => dataSets.filter(dataSet => dataSet.formType === 'CUSTOM').map(dataSet => ({
  id: dataSet.dataEntryForm.id,
  version: dataSet.version
})));

const useCustomForms = () => {
  return useQuery(keys.all, {
    select: selectCustomForms
  });
};

export default useCustomForms;